import React, { useEffect, useState } from 'react'
import { Link, useLocation } from 'react-router-dom'
import appendScript from "../../utils/appendScript"
import removeScript from "../../utils/removeScript"
import Hero from './Hero'
import { useSelector } from 'react-redux'

const Header = () => {
    const { key } = useLocation()
    useEffect(() => {
        appendScript("./assets/js/vendor.min.js")
        appendScript("./assets/js/theme.min.js")

        return () => {
            removeScript("./assets/js/vendor.min.js")
            removeScript("./assets/js/theme.min.js")
        }

    }, [key])

    const { isAuthenticated } = useSelector(state => state.auth)
    return (

        <header>
            <nav className="navbar navbar-expand-lg topnav-menu navbar-light zindex-10">
                <div className="container">
                    <a className="navbar-brand" href="/">
                        <img
                            src="./assets/images/logo.png"
                            height={50}
                            className="d-inline-block align-top"
                            alt=""
                        />
                    </a>
                    <a
                        href="#"
                        className="navbar-toggler"
                        data-bs-toggle="collapse"
                        data-bs-target="#topnav-menu-content3"
                        aria-controls="topnav-menu-content3"
                        aria-expanded="false"
                        aria-label="Toggle navigation"
                    >
                        <span className="navbar-toggler-icon" />
                    </a>
                    <div className="collapse navbar-collapse" id="topnav-menu-content3">
                        <ul className="navbar-nav align-items-lg-center mx-auto">
                            <li className="nav-item">
                                <Link className="nav-link" to="/">
                                    Home
                                </Link>
                            </li>

                            <li className="nav-item">
                                <Link className="nav-link" to="/aboutus">
                                    About Us
                                </Link>
                            </li>
                            <li className="nav-item">
                                <Link className="nav-link" to="/features">
                                    Our Features
                                </Link>
                            </li>
                            <li className="nav-item">
                                <Link className="nav-link" to="/developers">
                                    Developers
                                </Link>
                            </li>

                            <li className="nav-item">
                                <Link
                                    className="nav-link"
                                    to="/contact"
                                    id="navbarLandings"
                                    role="button"

                                >
                                    Contact Us

                                </Link>
                            </li>
                        </ul>
                        <ul className="navbar-nav">
                            {/* <li className="nav-item">
                                    {isAuthenticated ? <Link className="btn btn-primary btn-sm" to="/login">
                                        View Dashboard
                                    </Link> : <Link className="btn btn-primary btn-sm" to="/login">
                                        Launch App
                                    </Link>}
                                </li> */}
                            <li className="nav-item">
                                {isAuthenticated ? <Link className="btn btn-primary btn-sm" to="/login">
                                    View Dashboard
                                </Link> : <a className="btn btn-primary btn-sm" href="https://staging.paytez.io/login">
                                    Launch App
                                </a>}
                            </li>
                        </ul>
                    </div>
                </div>
            </nav>
        </header>



    )
}

export default Header
