import React from 'react'
import { useNavigate } from 'react-router-dom'

const Hero = () => {
    const navigate = useNavigate()
    return (
        <section className="hero-4 pt-7 py-sm-4">
            <div className="container">
                <div className="row align-items-center">
                    <div className="col-lg-5 col-md-6">
                        <h1 className="hero-title">
                            <span className="highlight highlight-warning d-inline-block">
                                Paytez
                            </span> {" "}
                            Power Up {" "}

                            your DApp
                        </h1>
                        <p className="mt-4 fs-17">
                            Paytez, allows wallet setup using social login, so onboarding customers to your dapp is now that simple.
                        </p>
                        <div className="pt-3 pt-sm-5">
                            <button onClick={() => {
                                navigate("/login")
                            }} className="btn btn-primary">Create Account</button>
                            <button onClick={() => {
                                navigate("/aboutus")
                            }} className="btn btn-outline-primary ms-2">Know More</button>
                            {/* <a href="https://staging.paytez.io/login" className="btn btn-primary">Create Account</a> */}
                            {/* <a href="https://staging.paytez.io/aboutus" className="btn btn-outline-primary ms-2">Know More</a> */}
                            {/* <button onClick={() => {
                                navigate("/about")
                            }} className="btn btn-outline-primary ms-2">Know More</button> */}
                        </div>
                    </div>
                    <div className="col-lg-7 col-md-6">
                        <div className="img-container text-end pt-5 pt-sm-0">
                            <img
                                src="./invoices-pana.svg"
                                alt=""
                                className="img-fluid"
                                data-aos="fade-left"
                                data-aos-duration={1000}
                            />
                        </div>
                    </div>
                </div>
            </div>
            <div className="shape bottom">
                <svg
                    width="1440px"
                    height="40px"
                    viewBox="0 0 1440 40"
                    version="1.1"
                    xmlns="http://www.w3.org/2000/svg"
                    xmlnsXlink="http://www.w3.org/1999/xlink"
                >
                    <g
                        id="shape-b"
                        stroke="none"
                        strokeWidth={1}
                        fill="none"
                        fillRule="evenodd"
                    >
                        <g id="curve" fill="#fff">
                            <path
                                d="M0,30.013 C239.659,10.004 479.143,0 718.453,0 C957.763,0 1198.28,10.004 1440,30.013 L1440,40 L0,40 L0,30.013 Z"
                                id="Path"
                            />
                        </g>
                    </g>
                </svg>
            </div>
        </section>
    )
}

export default Hero
